.G-filter {
    width: 100%;
    display: flex;
    position: relative;
    &_header {

        &_icon {
            > * {
                margin-top: 8px;
                width: 20px;
                height: 20px;
            }
        }

        &_icon:hover {
            transform: scale(1.2);
        }
    }

    &_body {
        width: 100%;
        height: 50px;
        opacity: 0;
        overflow: hidden;
        padding-left: 20px;
        display: flex;
        align-items: start;
        transition: .7s ease;
        &_show {
            opacity: 1;
            transition: .7s ease;
        }

        &_row {
            margin-right: 20px;
        }
    }
}

.input-row {
    border: 2px solid #E9EBEE;
    border-radius: 7px;
    display: flex;
    justify-content: space-between;
    align-items: center;
    padding: 0 15px;

    > input {
        border: none;
        font-size: 16px;
        outline: none;       
        padding: 7px 0; 
    }

}

.checkbox-row {
    display: flex;
    align-items: center;

    > label {
        font-size: 14px;
        text-transform: uppercase;
        cursor: pointer;
        margin-left: 15px;
        color: rgb(51, 90, 39);
        user-select: none;
    }

    > input {
        width: 20px;
        height: 20px;
        border: rgb(51, 90, 39);
    }
}

.calendar-row {
    display: flex;
    align-items: center;
    justify-content: space-between;
    > span {
        display: block;
        margin-right: 30px;
        padding: 7px 11px;
        cursor: pointer;
        user-select: none;
        border: 2px solid #E9EBEE;

    }
}

.G-calendar {
    border: 5px solid red;
    position: absolute;
    top: 100%;
}