@import url('https://fonts.googleapis.com/css2?family=Ubuntu:wght@300&display=swap');

* {
    box-sizing: border-box;
}

body {
    margin: 0;
}

.container {
    padding: 0 100px;
}

.G-cursor {
    cursor: pointer;
}
.G-flex {
    display: flex;
}

.G-align-end {
    align-items: flex-end;
}

.G-justify-end {
    justify-content: flex-end;
}

.G-width-full {
    width: 100%;
}

.G-page {
    margin-top: 100px;
}
