@import "https://cdn.jsdelivr.net/npm/bootstrap@5.1.3/dist/css/bootstrap.min.css";
.page {
    

    &_title {
        font-family: 'Ubuntu', sans-serif;
        font-size: 24px;
        width: 32%;
    }

    &_pagination-container {
        margin: 50px 0;
        width: 100%;
        display: flex;
        align-items: center;
        justify-content: flex-end;

    }
}

.page-link {
    user-select: none !important;
}

.add_user_button {
    margin: 0 50px 0 0 ;
    padding: 5px 7px;
    border: 2px solid rgb(0, 128, 0);
    cursor: pointer;
    font-size: 14px;
    text-transform: uppercase;
    font-weight: bold;
    width: 100px;
    text-align: center;
}

.add_user_button:hover {
    transform: scale(1.1);
}