.logo {
    display: flex;
    flex-direction: column;
    cursor: pointer ;
    > div {
        color: rgb(51, 90, 39);
        font-size: 24px;
        font-weight: 700;
        font-family: 'Ubuntu', sans-serif;
        user-select: none;
    }
    > span {
        color: rgb(46, 66, 40);
        font-size: 18px;
        font-weight: 500;
        font-family: 'Ubuntu', sans-serif;
        user-select: none;
    }
}