.create-user_modal {

    &_title {
        color: rgb(51, 90, 39);
        font-size: 16px;
        font-weight: bold;
        font-family: sans-serif;
        margin-bottom: 15px;
        border-bottom: 2px dashed grey;
        display: flex;
        align-items: center;
        justify-content: space-between;
    }

    &_form {
        width: 600px;
        display: flex;
        flex-wrap: wrap;
        .modal_form_item {
            flex: 1;
            margin-bottom: 20px;

            > input {
                font-size: 14px;
                outline: none;
            }

            > label {
                user-select: none;
            }

            > div > label {
                user-select: none;
            }
        }

        .modal_form_item_radio {
            display: flex;
            align-items: center;
            > div:first-child {
                margin-right: 10px;
            }
            > div > label {
                margin-right: 7px;
            }
        }

        .modal_form_item_checkbox {
            display: flex;
            align-items: center;
            > label {
                margin-right: 7px;
            }

        }

    }

    &_buttons {
        display: flex;
        justify-content: flex-end;
        > button {
            margin-left: 17px;
            font-size: 12px;
            text-transform: uppercase;
            font-weight: bold;
            padding: 4px 6px;
            background: transparent;
        }

        &_cancel {
            border: 2px solid red;
            color: red;
        }

        &_apply {
            border: 2px solid rgb(51, 90, 39);
            color: rgb(51, 90, 39);
        }
    }

}

.modal_close_button {
    color: black;
}