.login {
    width: 100%;
    height: 100vh;
    display: flex;
    align-items: center;
    justify-content: center;
    
    &_form {
        width: 30%;
        padding: 30px 40px;
        box-shadow: 2px 5px 28px 19px rgba(0,0,0,0.13);
        border-radius: 14px;

        &_logo {
           margin-bottom: 30px;
        }

        &_element {
            display: flex;
            flex-direction: column;
            margin: 25px 0;
            > label {
                font-size: 14px;
                font-family: 'Ubuntu', sans-serif;
                font-weight: bold;
            }

            > input {
                padding: 7px 10px;
                border: 2px solid #E9EBEE;
                outline: none;
                color: #000;
                border-radius: 7px;
                margin-top: 10px;
            }
        }

        &_button {
            width: fit-content;
            padding: 7px 10px;
            border: 2px solid rgb(51, 90, 39);
            // background: rgb(114, 247, 74);
            border-radius: 7px;
            font-size: 16px;
            cursor: pointer;
            font-weight: bold;
            font-family: 'Ubuntu', sans-serif;
        }
    }
}