.G-modal {
    width: 100vw;
    height: 100vh;
    position: fixed;
    top: 0;
    left: 0;
    right: 0;
    bottom: 0;
    background: rgba(0, 0, 0, .3);
    z-index: 11;
    display: flex;
    justify-content: center;
    align-items: center;

    > div {
        width: fit-content;
        padding: 30px 40px;
        background: #fff;
        z-index: 12;
        border-radius: 14px;
    }
}